@font-face {
  font-family: 'Bungee Spice Regular';
  src: url('./assets/fonts/BungeeSpice-Regular.ttf');
}

@font-face {
  font-family: 'Dongle';
  src: url('./assets/fonts/Dongle-Regular.ttf');
}

:root {
  --font-dongle: 'dongle';
  --font-bungee: 'Bungee Spice Regular';
  --menus-height: 17rem;
}

* {
  font-family: var(--font-dongle);
}

body {
  margin: 0;
  box-sizing: border-box;
  background-color: whitesmoke;
